@use 'colors';
@use 'base';
@use 'corners';
@use 'menu';
@use 'body';


.aside {
    display: flex;
    flex-direction: column;
    justify-content: center;
    
    @media (min-width: 660px) {
        width: 300px;
        min-height: 1000px;
        flex-shrink: 0;
        justify-content: flex-start;
        padding: 20px 30px 20px 20px;
        // position: fixed;

        
    }
}

.aside-vv {
    @media (min-width: 660px) {
        background-image: url(/images/aside_background_light.png);
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
    }
}

.aside-gd {
    @media (min-width: 660px) {
        background-image: url(/images/aside_background_dark.png);
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;

    }
    
}
.aside-nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    @media (min-width: 660px) {
        justify-content: flex-start;
    }
}







.aside-name--vv {
    color: colors.$vv;
    font-size: 20px;
    font-weight: 600;
    padding: 10px 0;
    margin: 10px auto;
    text-transform: uppercase;
    @media (min-width: 660px) {
        font-size: 25px;
        text-align: center;
        padding: 0;
    }


    &::after {
        content: '';
        display: block;
        width: 80px;
        height: 2px;
        background-color: lightgray;
        margin: 10px auto 0 auto;
    }
}



.aside-name--gd {
    color: colors.$gd;
    font-weight: 400;
    padding: 10px 0;
    margin: 10px auto;
    text-transform: uppercase;

    @media (min-width: 660px) {
        font-size: 25px;
        text-align: center;
        padding: 0;
    }

    &::after {
        content: '';
        display: block;
        width: 80px;
        height: 2px;
        background-color: colors.$grey-middle;
        margin: 10px auto 0 auto;
    }
}


.aside-item {
    &--vv, &--gd {
        max-width: 200px;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 20px 20px;
        padding: 15px;
        border-radius: 5px;

    
        @media (min-width: 660px) {
        width: 100%;
        padding: 15px;
        flex-direction: row;
        align-items: flex-start;      
        }
    }

    &--vv {
        background-color: rgba(white, 0.4);
        box-shadow: 2px 2px 20px rgba(colors.$grey-dark, 0.2);
    }

    &--gd {
        background-color: rgba(colors.$grey-superdark, 0.5);
        box-shadow: 2px 2px 20px rgba(colors.$grey-dark, 0.7);
    }

    &__icon-gd, &__icon-vv {
        min-width: 60px;  
    }

    &__category-gd, &__category-vv {
        font-size: 15px;
        text-align: center;
        padding: 0;
        margin: 10px 0 0 0;
        display: block;

        @media (min-width: 660px) {
          margin: 10px 0 10px 15px;  
          text-align: left;
    }
    }
}

.aside-item__description {
    padding: 20px;
    font-size: 14px;
}









//  :hover aside-item - vv -----------------------

.aside-item--vv:hover {
    background-color: white;
    box-shadow: 2px 2px 10px rgba(colors.$grey-dark, 0.2);
}   

.aside-item--vv:hover .aside-item__icon-vv {
    fill: colors.$vv;
    stroke: colors.$vv;;  
}

.aside-item--vv:hover .aside-item__category-vv {
    color: colors.$vv;
}
// ----------------------------------------------

// :active aside-item vv------------------------

.aside-item--vv:active {
    background-color: white;
    box-shadow: 2px 2px 20px rgba(colors.$grey-middle, 0.3);
} 

.aside-item--vv:active .aside-item__icon-gd {
    fill: colors.$vv2;
}

.aside-item--vv:active .aside-item__category-gd {
    color: colors.$vv2;
}

// -----------------------------------------






.aside-item--gd {
    fill: white;
}

.aside-item__category-gd {
    color: white;
    font-weight: 400;
}

// :hover aside-item gd------------------------

.aside-item--gd:hover {
    background-color: rgba(rgb(20, 20, 20), 0.5);
    box-shadow: 2px 2px 20px rgba(colors.$grey-middle, 0.5);
} 

.aside-item--gd:hover .aside-item__icon-gd {
    fill: colors.$gd;
}

.aside-item--gd:hover .aside-item__category-gd {
    color: colors.$gd;
}

// -----------------------------------------

// :active aside-item gd------------------------

.aside-item--gd:active {
    background-color: rgba(rgb(20, 20, 20), 0.5);
    box-shadow: 2px 2px 20px rgba(colors.$grey-middle, 0.3);
} 

.aside-item--gd:active .aside-item__icon-gd {
    fill: colors.$gd2;
}

.aside-item--gd:active .aside-item__category-gd {
    color: colors.$gd2;
}

// -----------------------------------------


// indside link

.inside-link {
    text-decoration: none;
    line-height: 1.3;
    color: black;
}